import { Node, Controller } from '@ali/4ever-cangjie';
import { isTableNode } from '../../utils//utils/isTableNode';

/**
 * 判断节点是否有 parent 属于表格/分栏（不包含节点本身）
 */
export default function isNodeInTable(
  controller: Controller,
  node: Node | null,
  excludeColumns = false,
) {
  if (!node) return false;
  const { document } = controller.value;
  let parent = document.getParent(node.key);
  while (parent && !isTableNode(parent, excludeColumns)) {
    parent = document.getParent(parent.key);
  }
  return Boolean(parent);
}
